<template>
  <transition name="slide-y-transition">
    <v-dialog v-if="date" ref="timeDialog" v-model="timeModal" :return-value.sync="selectedTime" persistent lazy full-width width="290px">
      <v-text-field slot="activator" :rules="[rules.required]" v-model="selectedTime" color="green darken-3" label="Час завершення завдання" prepend-icon="access_time" readonly></v-text-field>
      <v-time-picker format="24hr" :min="timeMin" scrollable v-model="selectedTime" color="green darken-3" actions>
        <v-spacer></v-spacer>
        <v-btn flat color="grey--text text--darken-2" @click="cancel">Скасувати</v-btn>
        <v-btn dark color="green darken-3" @click="onSelectedTime">OK</v-btn>
      </v-time-picker>
    </v-dialog>
  </transition>
</template>

<script>
export default {
  props: ["onTime", "date"],
  data() {
    return {
      selectedTime: null,
      timeModal: false,
      rules: {
        required: value => !!value || "Обов'язкове поле."
      }
    };
  },
  watch: {
    date(val) {
      if (!val) {
        this.selectedTime = null;
        this.$refs.timeDialog.save(this.selectedTime);
        this.onTime(this.selectedTime);
      }
    }
  },
  computed: {
    timeMin() {
      const now = new Date();
      let hours = now.getHours();
      let minutes = now.getMinutes();
      hours = hours < 10 ? `0${hours}` : hours;
      minutes = minutes < 10 ? `0${minutes}` : minutes;
      if (this.dateIsNow) {
        return `${hours}:${minutes}`;
      }
      return "";
    },
    dateIsNow() {
      const now = new Date();
      const selectedDate = new Date(Date.parse(this.date));
      const nowDay = now.getDate();
      const nowMonth = now.getMonth();
      const selectedDay = selectedDate.getDate();
      const selectedMonth = selectedDate.getMonth();
      if (nowDay == selectedDay && nowMonth == selectedMonth) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    onSelectedTime() {
      this.$refs.timeDialog.save(this.selectedTime);
      this.onTime(this.selectedTime);
    },
    cancel() {
      this.timeModal = false;
    }
  }
};
</script>



